
import XHeader from 'vux/src/components/x-header/index.vue'

import { imgPreview } from "@/assets/js/img.js";

export default {
name: "FileLogin",
components: {
XHeader
},
data() {
return {
showBtn: true,
showBtn1: true,
showBtn2: true,
username: "",
idcard: ""
};
},
mounted() {
var routerParams = this.$route.params;
this.username = routerParams.username;
this.idcard = routerParams.idcard;
},
methods: {



















sendData(type) {
var json = "{type:'2',photoType:" + type + "}";
if (this.$root.phoneType == "ios") {
window.webkit.messageHandlers.mutual.postMessage({type: "2", photoType: type});
} else {
android: AndroidFun.mutual(json);
}
},
/**
* 提交实人认证信息
*/
updataCommidIdCard() {
this.$ajax
.post("", {
"MSH.1": "user",
"MSH.2": "personalAuthentication",
token: this.$root.loginInfo.token,
name: this.username,
idcardNo: this.idcard,
idcardZm: this.$root.idCardphoto1,
idcardFm: this.$root.idCardphoto2,
manImg: this.$root.idCardphoto3,
CHZBM: this.$root.loginInfo.CHZBM,
grdaid: this.$root.loginInfo.grdaid
})
.then(data => {
if (typeof data != "undefined") {
if(data.status==1){
this.$root.loginInfo.CSFZH = this.idcard;
this.$root.loginInfo.CHZXM = this.username;
this.$root.loginInfo.CSMZT = "3";
this.$router.push("/uploadDone");
}
}
})
.catch(error => {
console.log(error);
});
}
},
watch: {
"$root.idCardphoto1"(newVal1) {
this.$refs.idCard.style.backgroundImage = `url(${ this.$root.hostImage+this.$root.idCardphoto1 })`;
this.showBtn = false;
},
"$root.idCardphoto2"(newVal2) {
this.$refs.idCard1.style.backgroundImage = `url(${
this.$root.hostImage+this.$root.idCardphoto2
})`;
this.showBtn1 = false;
},
"$root.idCardphoto3"(newVal3) {
this.$refs.idCard2.style.backgroundImage = `url(${
this.$root.hostImage+this.$root.idCardphoto3
})`;
this.showBtn2 = false;
}
}
};



